
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
data() {
return {
noData: false,
list: [],
};
},
mounted() {

this.getChildbirthList();
},
methods: {

srcValue(TYPE) {
if (TYPE == 1) {
return require("../../imgs/HealthRecords/small_house_pink.png");
} else if (TYPE == 2) {
return require("../../imgs/HealthRecords/small_doctor_pink.png");
}
},

getChildbirthList() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "fenmianService",
"MSH.2": "list",
'GRDAID':this.$root.loginInfo.grdaid,
"CHZBM": this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined') {
this.list = [];
if (res.length > 0) {
this.noData = false;
this.list = res;
} else {
this.noData = true;
}
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.noData = true;
})
},

gotoDetail(TYPE, CBH) {
if (TYPE == 1) {
this.$router.push({name: "descChildbirthInfo", params: {TYPE: TYPE, CBH: CBH}});
} else if (TYPE == 2) {
this.$router.push({name: "descChildbirthHelp", params: {TYPE: TYPE, CBH: CBH}});
}
}
},
components: {
XHeader,
}
};



